import {
  Component,
  ViewContainerRef,
  ComponentFactoryResolver,
  AfterViewInit,
  QueryList,
  ViewChildren,
} from '@angular/core';
import { delay, tap, debounceTime } from 'rxjs/operators';
import { MainTab } from '../../../../shared/definitions';
import { TabService } from '../../../../shared/services/tabs.service';

@Component({
  selector: 'app-tab',
  templateUrl: './tab.component.html',
  styleUrls: ['./tab.component.scss'],
})
export class TabComponent implements AfterViewInit {
  public tabItems: MainTab[] = [];

  @ViewChildren('content', { read: ViewContainerRef })
  viewContainerRef: QueryList<ViewContainerRef>;
  public contentRef;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private tabsService: TabService
  ) {
    this.tabItems = this.tabsService.tabs.getValue();
    this.tabsService.onTabAdded
      .pipe(
        tap((tab: any) => {
          this.tabItems = [...this.tabItems, tab];
        }),
        delay(50),
        tap((tab) => {
          const contents = this.viewContainerRef.toArray();
          this.renderBody(tab, contents[contents.length - 1]);
        })
      )
      .subscribe();

    this.tabsService.onTabRemoved.subscribe((tab: MainTab) => {
      this.tabItems = this.tabItems.filter((t) => t.id !== tab.id);
    });
  }

  ngAfterViewInit() {
    if (this.tabItems && this.tabItems.length > 0) {
      const contents = this.viewContainerRef.toArray();
      this.renderBody(this.tabItems[0], contents[0]);
    }
  }

  public trackBy(index, item) {
    return item.id;
  }

  renderBody(tab, content): void {
    if (!tab.content) {
      return;
    }

    const factory = this.componentFactoryResolver.resolveComponentFactory(
      tab.content
    );

    this.contentRef = content.createComponent(factory);

    if (tab.params) {
      this.contentRef.instance.params = tab.params;
    }
    this.contentRef.changeDetectorRef.detectChanges();
  }
}
