import { Component, Input, ViewChild } from '@angular/core';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-csv-import-preview',
  templateUrl: './csv-import-preview.component.html',
})
export class CSVImportPreviewComponent {
  @ViewChild(CdkVirtualScrollViewport, { static: false })
  public viewPort: CdkVirtualScrollViewport;

  public get inverseOfTranslation(): string {
    if (!this.viewPort || !this.viewPort['_renderedContentOffset']) {
      return '-2px';
    }

    let offset = this.viewPort['_renderedContentOffset'];
    return `-${offset + 2}px`;
  }

  public scroll(index: number): void {
    this.viewPort.scrollToIndex(index);
  }

  @Input() public fieldNames = [];
  @Input() public include: boolean[] = [];
  @Input() public dataTypes = [];
  @Input() public skipRows = 1;
  @Input() public contentPreview: any = null;
  @Input() public contentTypeGuess: any[] = [];
  @Input() public isImporting: boolean;
  @Input() public jobId: number = 0;
  @Input() public linesProcessed: number = 0;
}
