import { Injectable } from '@angular/core';
import { AppRoutes } from 'app/router';
import { translations } from 'app/locale/translations';
import { DeviceRichInformation } from '../../../../ipc-helpers/definitions';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class NavigatorService {
  public translations = translations;
  public navigateSubject = new Subject();

  go(title: string, path: AppRoutes, params = {}): void {
    this.navigateSubject.next({ title, path, params });
  }

  openSerialPort(path: string): void {
    this.go(translations.tabsTitle.serialPort, AppRoutes.serialMonitoring, {
      port: encodeURIComponent(path),
    });
  }

  public openActiveStreamsTab(): void {
    this.go('Network and streams', AppRoutes.activeStreams);
  }

  public openRealtimeTab(): void {
    this.go('Realtime', AppRoutes.realtime);
  }

  public openDevices(): void {
    this.go('Devices', AppRoutes.devices);
  }

  public openPort(): void {
    this.go('Open port', AppRoutes.openPort);
  }

  public openNewDevice(): void {
    this.go('New device', AppRoutes.deviceNew);
  }

  public openDatabaseConfig(): void {
    this.go('Database configuration', AppRoutes.database);
  }

  public openSettings(): void {
    this.go(translations.tabsTitle.settings, AppRoutes.settings);
  }

  public openLogicBuilding(): void {
    this.go('Logic building', AppRoutes.logicBuilding);
  }

  public openTemperatureView(): void {
    this.go('Temperature View', AppRoutes.temperatureView);
  }

  public deviceEdit(id: number) {
    this.go('Edit device', AppRoutes.deviceEdit, {
      device: id,
    });
  }

  public openViewDeviceTab(device: DeviceRichInformation): void {
    const title = `Device ${device.imei || 'unknown'} (${device.id})`;
    this.go(title, AppRoutes.deviceView, { id: device.id });
  }

  public openImportCSVTab(file = null): void {
    const title = `Import data`;
    this.go(title, AppRoutes.importCsv, { file });
  }
}
