import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavigatorService } from 'app/shared/services/navigator.service';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss'],
})
export class LayoutComponent {
  @ViewChild('inputFile') fileuploader: ElementRef;
  public windowUploader = false;
  public files: Array<any> = [];

  constructor(private navigatorService: NavigatorService) {}

  uploadHandler(): void {
    for (const file of this.files) {
      this.navigatorService.openImportCSVTab(file);
    }
  }

  eventHandler(e) {
    const ele = e.currentTarget.getBoundingClientRect();
    const x = e.clientX - ele.left;
    const y = e.clientY - ele.top;
    switch (e.type) {
      case 'dragover':
        e.preventDefault();
        break;
      case 'dragenter':
        e.preventDefault();
        this.windowUploader = true;
        break;
      case 'dragleave':
        if (x >= ele.width || y >= ele.height || x < 0 || y < 0) {
          this.windowUploader = false;
        }
        break;
    }
  }

  setToFileUploader(e) {
    e.preventDefault();
    this.windowUploader = false;
    this.files = e.dataTransfer.files;
    this.uploadHandler();
  }
}
