import { Location } from '@angular/common';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { translations } from 'app/locale/translations';
import { DataBlender, BuiltinDataBlenders, applyBlend } from './blendtools';
import { NavigatorService } from 'app/shared/services/navigator.service';
import { NotificationService } from 'app/shared/services/notification.service';
import { IResponse } from 'response-type';
import { Subscription } from 'rxjs';
import { tap } from 'rxjs/operators';
import { DeviceRichInformation } from '../../../../../ipc-helpers/definitions';
import { DataService } from '../../../shared/services/data.service';
import { random } from 'lodash';

const devicePresets = [
  {
    value: 'custom',
    label: 'Custom device',
  },
  {
    value: 'temperature',
    label: 'General temperature',
  },
  {
    value: 'ovio_temperature',
    label: 'OVIO Temperature',
  },
];

const listOfBlendSequence: Array<DataBlender<any, any>> = [
  new BuiltinDataBlenders.ExtractNumberBlender(),
  new BuiltinDataBlenders.TakeFirstBlender(),
  new BuiltinDataBlenders.DoubleBlender(),
  // new BuiltinDataBlenders.MathBlender(),
  // new BuiltinDataBlenders.JsonBlender(),
];

@Component({
  selector: 'app-device-single',
  templateUrl: './device-single.component.html',
  styleUrls: ['./device-single.component.scss'],
})
export class DeviceSingleComponent implements OnInit, OnDestroy {
  private subscriptions: Subscription[] = [];
  public res: IResponse<DeviceRichInformation>;
  public devicePresets = devicePresets;
  public translations = translations;

  @Input() public params: { device?: number } = {};

  public sampleInput = new FormControl('Temperature 100');

  public form = new FormGroup({
    name: new FormControl(''),
    preset: new FormControl('custom'),
    imei: new FormControl(''),
    id: new FormControl(''),
  });

  constructor(
    private dataService: DataService,
    private notification: NotificationService,
    private location: Location,
    private navigatorService: NavigatorService
  ) {}

  ngOnInit(): void {
    this.getDeviceHistory();
  }

  public get descriptionLabel(): string {
    return this.form.value.id
      ? translations.deviceForm.updateDescription
      : translations.deviceForm.createDescription;
  }

  public get titleLabel(): string {
    return this.form.value.id
      ? translations.deviceForm.updateTitle
      : translations.deviceForm.createTitle;
  }

  public get blend(): string {
    return applyBlend(this.sampleInput.value, listOfBlendSequence);
  }

  private get id(): number {
    return +this.params.device;
  }

  public get availableBlender() {
    return listOfBlendSequence;
  }

  private getDeviceHistory() {
    console.log(this.id);
    if (!this.id) {
      return;
    }

    this.dataService
      .getDevice(this.id)
      .pipe(
        tap((res) => {
          this.res = res;
          this.form.patchValue(res.data);
        })
      )
      .subscribe();
  }

  submit(): void {
    let id = this.form.value.id;
    this.dataService
      .postDevice(this.form.value)
      .pipe(
        tap((res) => {
          this.res = res;
          if (res.error) {
            this.notification.showFailedMessage(
              translations.deviceForm.errorSubmit,
              translations.serverErrors[res.error.message] ||
                res.error.message ||
                translations.deviceForm.errorDescription
            );
          } else if (res.data) {
            this.notification.showSuccessMessage(
              translations.deviceForm.submitUpdate,
              translations.deviceForm.submitDescription
            );

            this.navigatorService.openDevices();
          }
        })
      )
      .subscribe();
  }

  ngOnDestroy(): void {
    this.subscriptions
      .filter(Boolean)
      .forEach((subscription) => subscription.unsubscribe());
  }

  get submitLabel(): string {
    return this.form.value.id
      ? translations.deviceForm.update
      : translations.deviceForm.create;
  }
}
