import { Injectable, Renderer2, RendererFactory2 } from '@angular/core';
import { KEY_CODE } from '../definitions';
import { NavigatorService } from './navigator.service';

@Injectable({
  providedIn: 'root',
})
export class ShortkeyService {
  renderer: Renderer2;
  constructor(
    private rendererFactory2: RendererFactory2,
    private navigatorService: NavigatorService
  ) {
    this.renderer = this.rendererFactory2.createRenderer(null, null);
    this.renderer.listen('document', 'keyup', (event) => {
      const $event: any = document.all ? window.event : event;
      if (/^(?:input|textarea|select|button)$/i.test($event.target.tagName)) {
        return;
      }
      if (event.ctrlKey && event.keyCode == KEY_CODE.I) {
        this.navigatorService.openImportCSVTab();
      }

      if (event.ctrlKey && event.keyCode == KEY_CODE.C) {
        this.navigatorService.openDevices();
      }
    });
  }
}
