import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { IncomingMessageFromDevice } from '../../../../ipc-helpers/definitions';

@Injectable({ providedIn: 'root' })
export class RealtimeService {
  public lastMessageReceived: BehaviorSubject<
    IncomingMessageFromDevice<any>
  > = new BehaviorSubject(null);

  constructor() {}

  public handleIncomingData(data: IncomingMessageFromDevice<any>) {
    if (typeof data === 'object' && data && !data.timestamp) {
      data.timestamp = new Date().getTime() as any;
    }

    this.lastMessageReceived.next(data);
  }
}
