import {
  Component,
  OnInit,
  ElementRef,
  ViewChild,
  Output,
  EventEmitter,
} from '@angular/core';

@Component({
  selector: 'app-file-upload',
  templateUrl: './file-upload.component.html',
  styleUrls: [],
})
export class FileUploadComponent {
  @Output() upload = new EventEmitter();
  @ViewChild('inputFile') fileuploader: ElementRef;
  public windowUploader = false;
  public file: any;

  constructor() {}

  uploader() {
    const file = this.fileuploader.nativeElement.files[0];
    this.uploadHandler(file);
  }

  uploadHandler(file) {
    this.file = file;
    this.upload.emit(file);
  }

  eventHandler(e) {
    const ele = e.currentTarget.getBoundingClientRect();
    const x = e.clientX - ele.left;
    const y = e.clientY - ele.top;
    switch (e.type) {
      case 'dragover':
        e.preventDefault();
        break;
      case 'dragenter':
        e.preventDefault();
        this.windowUploader = true;
        break;
      case 'dragleave':
        if (x >= ele.width || y >= ele.height || x < 0 || y < 0) {
          this.windowUploader = false;
        }
        break;
    }
  }

  setToFileUploader(e) {
    e.preventDefault();
    this.windowUploader = false;
    this.uploadHandler(e.dataTransfer.files[0]);
  }
}
