import { Component } from '@angular/core';
import { IPCService } from 'app/shared/services/ipc.service';

@Component({
  selector: 'app-host-server',
  templateUrl: './host-server.component.html',
  styleUrls: ['./host-server.component.scss'],
})
export class HostServerComponent {
  constructor(private ipcService: IPCService) {}

  public promptInstallServer() {
    this.ipcService.requestInstall();
  }

  public promptUninstallServer() {
    this.ipcService.requestUninstall();
  }
}
