import { Component, OnDestroy, OnInit } from '@angular/core';
import { DataService } from '../../../shared/services/data.service';
import {
  DeviceRichInformation,
  IncomingMessageFromDevice,
} from '../../../../../ipc-helpers/definitions';

@Component({
  selector: 'app-dataflow',
  templateUrl: './dataflow.component.html',
  styleUrls: ['./dataflow.component.scss'],
})
export class DataFlowComponent implements OnInit, OnDestroy {
  public devices: DeviceRichInformation[] = [];
  public network: any;
  private subscriptions = [];
  public realtimeMessage: IncomingMessageFromDevice<any>;

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    // Here you see the all devices in the app, with their complete information
    // subscribe to this for ever if you need device changes.
    this.subscriptions = [
      this.dataService
        .devices$()
        .subscribe((items: DeviceRichInformation[]) => {
          this.devices = items;
        }),

      this.dataService
        .realtimeMessage$()
        .subscribe((message: IncomingMessageFromDevice<any>) => {
          this.realtimeMessage = message;
        }),
    ];
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((sub) => sub && sub.unsubscribe());
  }
}
