import { ApplicationRef, Injectable } from '@angular/core';
import { AppConfig } from 'environments/environment';
import { RealtimeService } from './realtime.service';
import { StreamService } from './stream.service';
import { StoreService } from './store.service';
import { DataService } from './data.service';

declare const io: any;

@Injectable({ providedIn: 'root' })
export class WebSocketService {
  constructor(
    private storeService: StoreService,
    private ref: ApplicationRef,
    private realtimeService: RealtimeService,
    private streamService: StreamService,
    private dataService: DataService
  ) {
    const socket = io(`ws://${AppConfig.HOST}:${AppConfig.PORT}`);

    socket.on('activeStreams', (data) => {
      if (Array.isArray(data)) {
        this.storeService.activeStreams.next(data);
      }
    });

    socket.on('SerialPortStreamData', (data) => {
      this.streamService.serialDataStream.next(data);
    });

    socket.on('rawMessage', (data) => {
      this.realtimeService.handleIncomingData(data);
    });

    socket.on('activeImports', (data) => {
      if (Array.isArray(data)) {
        this.storeService.activeImports.next(data);
      }
    });

    socket.on('disconnect', () => {
      this.storeService.bridgeConnected.next(false);
      this.ref.tick();
    });

    socket.on('connect', () => {
      this.storeService.bridgeConnected.next(true);
      this.dataService.triggerInternalIconTray().subscribe();
      this.ref.tick();
    });
  }
}
