import { Injectable } from '@angular/core';
import { MainTab, MainTabType } from '../definitions';
import { BehaviorSubject, Subject } from 'rxjs';
import { random } from 'lodash';
import { AppRoutes } from 'app/router';
import { tabRoutes } from '../../iot/components/main-tab/main-tab.router';
import { NavigatorService } from './navigator.service';
import { TemperatureViewComponent } from '../../iot/components/temperature-view/temperature-view.component';

@Injectable({
  providedIn: 'root',
})
export class TabService {
  public onTabAdded = new Subject();
  public onTabRemoved = new Subject();
  public onTabActived = new Subject();
  public params: any = {};
  public ls = JSON.parse(localStorage.getItem('tabs'));
  public allTabs: MainTab[] = this.ls || [];

  public tabs: BehaviorSubject<Array<MainTab>> = new BehaviorSubject(
    (this.allTabs = this.allTabs.map((v, i) => ({
      id: i + 1,
      active: i === 0,
      ...v,
    })))
  );

  constructor(private navigatorService: NavigatorService) {
    this.navigatorService.navigateSubject.subscribe((change: any) => {
      this.quickTab(change.title, change.path, change.params);
    });
    this.quickTab('Temperature Live Data', AppRoutes.temperatureView);
  }

  quickTab(title: string, path: AppRoutes, params = {}): void {
    const route = tabRoutes.find((x) => x.path === path);

    const newTab = {
      id: random(111111, 9999999),
      title,
      type: MainTabType.PRIMARY,
      content: route.component,
      params,
    };

    this.onTabAdded.next(newTab);

    this.allTabs.push(newTab);
    this.tabs.next(this.allTabs);
    this.activeTab(newTab);
  }

  removeTab(tab: MainTab) {
    if (this.allTabs.length > 0) {
      const tabindex = this.allTabs.findIndex((x) => x.id == tab.id);
      tab = this.allTabs[tabindex];
      this.allTabs = this.allTabs.filter((x) => x.id !== tab.id);
      this.tabs.next(this.allTabs);
      this.onTabRemoved.next(tab);
      console.log(tabindex, this.allTabs.length);
      if (tab.active) {
        let nextTab = 0;
        if (tabindex == 0) {
          nextTab = 0;
        } else if (tabindex == this.allTabs.length) {
          nextTab = this.allTabs.length - 1;
        } else {
          nextTab = tabindex;
        }
        this.activeTab(this.allTabs[nextTab]);
      }
    }
    this.storeTabInLocalStorage();
  }

  activeTab(tab: MainTab) {
    if (tab) {
      this.allTabs = this.allTabs.map((e) =>
        e.id == tab.id ? { ...e, active: true } : { ...e, active: false }
      );
      this.tabs.next(this.allTabs);
      this.onTabActived.next(tab);

      this.storeTabInLocalStorage();
    }
  }

  setTabParam(tab: MainTab) {
    if (tab.params) {
      const property = btoa(`/${tab.url}/${tab.id}`);
      this.params[property] = tab.params;
    }
  }

  getTabParam(url: string) {
    const property = btoa(url);
    return this.params[property];
  }

  activeTabById(index) {
    if (this.allTabs.length > 0) {
      this.activeTab(this.allTabs[index - 1]);
    }
  }

  tabFromRouter(url): MainTab {
    const urlSplit = url.split('/');
    const id = parseInt(urlSplit[urlSplit.length - 1]);
    return this.allTabs.find((x) => x.id === id);
  }

  nextTab() {
    const targetIndex = this.allTabs.findIndex((x) => x.active);
    if (targetIndex != this.allTabs.length - 1) {
      this.activeTab(this.allTabs[targetIndex + 1]);
    }
  }

  prevTab() {
    const targetIndex = this.allTabs.findIndex((x) => x.active);
    if (targetIndex != 0) {
      this.activeTab(this.allTabs[targetIndex - 1]);
    }
  }

  removeActiveTab() {
    const target = this.allTabs.find((x) => x.active);
    this.removeTab(target);
  }

  storeTabInLocalStorage() {
    // localStorage.setItem('tabs', JSON.stringify(this.allTabs));
  }
}
