/**
 * This service provides all information that application needs to work.
 * It means any http request, data collection, etc should go through this service.
 * Do not split it into module layer. It's not needed and intentionally all are placed in
 * the same place.
 */

import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

import {
  DeviceRichInformation,
  NetworkConnection,
} from '../../../../ipc-helpers/definitions';
import { ActivePortSubscription, SerialPort } from '../definitions';

@Injectable({ providedIn: 'root' })
export class StoreService {
  constructor() {}

  public networkInformation: BehaviorSubject<
    NetworkConnection[]
  > = new BehaviorSubject([]);

  public bridgeConnected: BehaviorSubject<boolean> = new BehaviorSubject(false);

  public devicesList: BehaviorSubject<
    DeviceRichInformation[]
  > = new BehaviorSubject([]);

  public activeStreams: BehaviorSubject<
    ActivePortSubscription[]
  > = new BehaviorSubject([]);

  public activeImports: BehaviorSubject<any[]> = new BehaviorSubject([]);

  public serialPorts: BehaviorSubject<SerialPort[]> = new BehaviorSubject([]);
}
