import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ActivePortSubscription, SerialPort } from 'app/shared/definitions';
import { StreamService } from 'app/shared/services/stream.service';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { DataService } from '../../../shared/services/data.service';

@Component({
  selector: 'app-serial-monitor',
  templateUrl: './serial-monitor.component.html',
  styleUrls: ['./serial-monitor.component.scss'],
})
export class SerialMonitorComponent implements OnInit, OnDestroy {
  public activeStreams: ActivePortSubscription[] = [];
  private subscriptions: Subscription[] = [];
  public serialPorts: SerialPort[] = [];

  public lines = [];

  constructor(
    private dataService: DataService,
    private route: ActivatedRoute,
    private streamService: StreamService
  ) {}

  ngOnInit(): void {
    const path = decodeURIComponent(this.route.snapshot.params.port);

    this.dataService.observeSerialPort(path).subscribe();

    this.streamService.serialDataStream
      .pipe(filter((content) => content.path === path))
      .subscribe((content) => {
        this.lines.unshift(content.data);
      });
  }

  ngOnDestroy(): void {
    this.subscriptions
      .filter(Boolean)
      .forEach((subscription) => subscription.unsubscribe());
  }
}
