import { Injectable } from '@angular/core';
import { StatusbarOptions, StatusbarStatus } from '../definitions';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class StatusbarService {
  public statusbarOptions: StatusbarOptions = {
    status: StatusbarStatus.WARNING,
  };
  public statusbar: BehaviorSubject<StatusbarOptions> = new BehaviorSubject(
    this.statusbarOptions
  );
  constructor() {}

  setStatus(status: StatusbarStatus) {
    if (this.statusbarOptions.status != status) {
      this.statusbarOptions.status = status;
      this.statusbar.next(this.statusbarOptions);
    }
  }
}
