import { uniq } from 'lodash';

export function isDate(str: string) {
  // 20200927T0000

  if (!str) {
    return false;
  }

  if (str.match(/[0-9]{8}T[0-9]{4}/)) {
    return true;
  }

  if (!isNaN(Date.parse(str))) {
    return true;
  }

  return false;
}

/**
 * Gives the best guess for headers of a csv file (namess)
 */
export function detectCSVHeader(
  rows: string[][]
): { items: string[]; skipRows: number } {
  let skipRows = 0;
  for (let row of rows) {
    if (row.length !== uniq(row).length) {
      skipRows++;
      continue;
    }

    return { items: row, skipRows };
  }

  return { items: rows[0], skipRows };
}

export function isNumeric(str: any) {
  if (typeof str !== 'string') return false;
  return !isNaN(str as any) && !isNaN(parseFloat(str));
}

/**
 * Detects the type of the data inside an array of csv string
 * as best guess. Send as many as rows you think is necessary
 */
export function detectDataType(rows: string[][]): any[] {
  const lastRow: string[] = rows[rows.length - 1];

  const types: any[] = [];
  for (let item of lastRow) {
    if (isNumeric(item)) {
      types.push('number');
      continue;
    }

    if (isDate(item)) {
      types.push('datetime');
      continue;
    }

    types.push('string');
  }

  return types;
}
