import { Injectable } from '@angular/core';
import { ToastService } from './toast.service';

@Injectable({
  providedIn: 'root',
})
export class NotificationService {
  constructor(private toastService: ToastService) {}

  showFailedMessage(title: string, message: string): void {
    this.toastService
      .open({ title, content: message, type: 'NOTIFY', status: 'ERROR' })
      .subscribe();
  }

  showSuccessMessage(title: string, message: string): void {
    this.toastService
      .open({ title, content: message, type: 'NOTIFY', status: 'SUCCESS' })
      .subscribe();
  }
}
