import { Component, OnInit } from '@angular/core';
import { ProgressBarService } from '../../../shared/services/progress-bar.service';
import { ProgressBar, ModalDialogType } from '../../../shared/definitions';
import { ProgressBarListComponent } from '../progress-bar-list/progress-bar-list.component';
import { ModalService } from 'app/shared/services/modal.service';
import { ImportService } from '../../../shared/services/import.service';

@Component({
  selector: 'app-progress-bar',
  templateUrl: './progress-bar.component.html',
  styleUrls: [],
})
export class ProgressBarComponent implements OnInit {
  public title: string;
  public precentage: number;

  public progressBars: Array<ProgressBar> = [];
  constructor(
    private progressBarService: ProgressBarService,
    private modalService: ModalService,
    private importService: ImportService
  ) {
    progressBarService.progressBar.subscribe((progressBars) => {
      this.progressBars = progressBars;
      this.title =
        this.progressBars.length === 1
          ? this.progressBars[0].title
          : this.progressBars.length + ' tasks are running...';
      this.precentage = this.progressBars.reduce(function (
        avg,
        value,
        _,
        { length }
      ) {
        return avg + value.precentage / length;
      },
      0);
    });
  }

  ngOnInit(): void {}

  public showAllProgressBars() {
    this.modalService.open({
      title: 'Running Tasks',
      content: ProgressBarListComponent,
      type: ModalDialogType.NOTIFY,
    });
  }

  public stopAllProgresses() {
    this.progressBars.forEach((e) => {
      e.stopFunction();
    });
  }
}
