import { Component, OnInit } from '@angular/core';
import { CSVImportComponent } from 'app/iot/components/csv-import/csv-import.component';
import { DataService } from 'app/shared/services/data.service';
import { ModalService } from 'app/shared/services/modal.service';
import { DeviceRichInformation } from '../../../../../ipc-helpers/definitions';
import { MainTab } from '../../../shared/definitions';
import { DeviceViewComponent } from '../device-view/device-view.component';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
})
export class DashboardComponent implements OnInit {
  public tabs: MainTab[] = [];

  constructor(
    public dataService: DataService,
    private modalService: ModalService
  ) {}

  public devices: DeviceRichInformation[] = [];

  public trackBy(index, item) {
    return item.id;
  }

  ngOnInit(): void {}

  public importCSV(): void {
    this.modalService
      .open({
        title: 'Import CSV',
        content: CSVImportComponent,
      })
      .subscribe();
  }
}
