import { Injectable } from '@angular/core';
import { ProgressBar } from '../definitions';
import { BehaviorSubject } from 'rxjs';
import { maxId } from '../utils';

@Injectable({
  providedIn: 'root',
})
export class ProgressBarService {
  public progressBars: Array<ProgressBar> = [];
  public progressBar: BehaviorSubject<Array<ProgressBar>> = new BehaviorSubject(
    this.progressBars
  );
  constructor() {}

  addProgress(progressBar: ProgressBar): ProgressBar {
    if (!progressBar.id) {
      progressBar.id = maxId(this.progressBars);
      this.progressBars.push(progressBar);
    } else {
      if (this.progressBars.find((x) => x.id === progressBar.id)) {
        this.progressBars = this.progressBars.map((e) =>
          e.id === progressBar.id ? progressBar : e
        );
      } else {
        this.progressBars.push(progressBar);
      }

      if (progressBar.precentage >= 100) {
        this.removeProgress(progressBar.id);
      }
    }

    this.progressBar.next(this.progressBars);
    return progressBar;
  }

  removeProgress(progressId: number): void {
    this.progressBars = this.progressBars.filter((x) => x.id !== progressId);
    this.progressBar.next(this.progressBars);
  }
}
