import { Component, Inject, NgZone, PLATFORM_ID, Input } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { random } from 'lodash';

// amCharts imports
import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import { SimpleTimeNumericChartEntry } from 'app/shared/definitions';
import { timer } from 'rxjs';
import { ChartRange } from '../../../shared/definitions';

@Component({
  selector: 'app-gauge-chart',
  templateUrl: './gauge-chart.component.html',
  styleUrls: ['./gauge-chart.component.scss'],
})
export class GaugeChartComponent {
  @Input() min: number = 0;
  @Input() max: number = 100;
  @Input() ranges: ChartRange[];

  private chart: am4charts.GaugeChart;
  public divId = `doc-${random(111111, 9999999)}`;
  public hand;

  constructor(@Inject(PLATFORM_ID) private platformId, private zone: NgZone) {}

  // Run the function only in the browser
  browserOnly(f: () => void) {
    if (isPlatformBrowser(this.platformId)) {
      this.zone.runOutsideAngular(() => {
        f();
      });
    }
  }

  ngAfterViewInit() {
    timer(100).subscribe(() => {
      this.drawChart();
    });
  }

  private drawChart(): void {
    // Chart code goes in here
    this.browserOnly(() => {
      am4core.useTheme(am4themes_animated);

      let chart = am4core.create(this.divId, am4charts.GaugeChart);
      chart.hiddenState.properties.opacity = 0;
      chart.innerRadius = -25;

      let axis = chart.xAxes.push(
        new am4charts.ValueAxis<am4charts.AxisRendererCircular>()
      );
      axis.min = this.min;
      axis.max = this.max;
      axis.strictMinMax = true;
      axis.renderer.grid.template.stroke = new am4core.InterfaceColorSet().getFor(
        'background'
      );
      axis.renderer.grid.template.strokeOpacity = 0.3;

      let colorSet = new am4core.ColorSet();

      if (!this.ranges || this.ranges.length === 0) {
        let range0 = axis.axisRanges.create();
        range0.value = this.min;
        range0.endValue = this.max;
        range0.axisFill.fillOpacity = 1;
        range0.axisFill.fill = colorSet.getIndex(0);
        range0.axisFill.zIndex = -1;
      }

      if (this.ranges) {
        this.ranges.forEach((range, i) => {
          let newRange = axis.axisRanges.create();
          newRange.value = range.value;
          newRange.endValue = range.endValue;
          newRange.axisFill.fillOpacity = 1;
          newRange.axisFill.fill = range.color
            ? am4core.color(range.color)
            : colorSet.getIndex(i);
          newRange.axisFill.zIndex = -1;
        });
      }

      this.hand = chart.hands.push(new am4charts.ClockHand());

      this.chart = chart;
    });
  }
  ngOnDestroy() {
    // Clean up chart when the component is removed
    this.browserOnly(() => {
      if (this.chart) {
        this.chart.dispose();
      }
    });
  }

  addData(value: any): void {
    if (!this.chart) {
      return;
    }

    this.hand.showValue(value, 500, am4core.ease.cubicOut);
  }

  reset() {
    this.chart.removeData(this.chart.data.length);
  }
}
