import { AppConfig } from '../../../environments/environment';

export const API = (affix: string = null) =>
  AppConfig.API + (affix ? '/' + affix : '');

export function IsMobile() {
  if (
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
      navigator.userAgent
    )
  ) {
    return true;
  }
  return false;
}

