import {
  Component,
  AfterContentInit,
  AfterContentChecked,
  ContentChildren,
  QueryList,
  Input,
  ViewChild,
  ViewChildren,
  ViewContainerRef,
  ElementRef,
} from '@angular/core';
import { MainTabItemComponent } from './../main-tab-item/main-tab-item.component';
import { Observable, of } from 'rxjs';
import { startWith, map, delay, debounceTime } from 'rxjs/operators';
import {
  PerfectScrollbarConfigInterface,
  PerfectScrollbarComponent,
} from 'ngx-perfect-scrollbar';
import { TabService } from '../../../../shared/services/tabs.service';
import { MainTab } from '../../../../shared/definitions';

@Component({
  selector: 'app-main-tabs',
  templateUrl: './main-tabs.component.html',
  styleUrls: [],
})
export class MainTabsComponent
  implements AfterContentInit, AfterContentChecked {
  @ViewChild('scrollframe', { static: false }) scrollFrame: ElementRef;
  @Input() fullWidth: boolean;
  @Input() public set activeTabIndex(value: number) {
    this._activeTabIndex = value || 0;
    try {
      const items = this.tabs.toArray();
      this.selectTab(items[this._activeTabIndex]);
    } catch (e) {}
  }
  @ViewChild(PerfectScrollbarComponent, { static: false })
  componentRef?: PerfectScrollbarComponent;
  @ContentChildren(MainTabItemComponent) tabs: QueryList<MainTabItemComponent>;
  tabItems$: Observable<MainTabItemComponent[]>;

  @ViewChildren('tab', { read: ViewContainerRef }) viewContainerRef: QueryList<
    ViewContainerRef
  >;

  activeTab: MainTabItemComponent;
  activeTabService: MainTab;
  _activeTabIndex: number;

  public isHover = false;
  public config: PerfectScrollbarConfigInterface = {
    suppressScrollX: false,
    suppressScrollY: true,
  };

  constructor(private tabService: TabService) {
    tabService.onTabActived
      .pipe(debounceTime(100))
      .pipe(delay(10))
      .subscribe((tab: MainTab) => {
        this.selectTab(this.tabs.find((x) => x.tab.id === tab.id));
        this.activeTabService = tab;
        this.scrollToEnd();
      });

    tabService.onTabRemoved.pipe(delay(10)).subscribe(() => {
      this.scrollToEnd();
    });
  }

  ngAfterContentInit(): void {
    of(true)
      .pipe(delay(1))
      .subscribe(() => {
        if (this.tabs && this.tabs.length > 0) {
          const tabItem = this.tabs.first;
          this.activeTab = tabItem;
          tabItem.isActive = true;
          this.activeTabService = tabItem.tab;
        }
      });
    this.setTabItems();
    this.scrollToEnd();
  }

  public trackBy(index, item) {
    return item.id;
  }

  setTabItems() {
    this.tabItems$ = this.tabs.changes
      .pipe(startWith(''))
      .pipe(delay(0))
      .pipe(map(() => this.tabs.toArray()));
  }

  ngAfterContentChecked() {
    if (!this.activeTab) {
      Promise.resolve().then(() => {
        this.activeTab = this.tabs.first;
      });
    }
  }

  selectTab(tabItem: MainTabItemComponent) {
    if (this.activeTab === tabItem || !tabItem) {
      return;
    }
    if (this.activeTab) {
      this.activeTab.isActive = false;
    }
    this.activeTab = tabItem;
    tabItem.isActive = true;
  }

  selectTabService(tabItem: MainTabItemComponent, e) {
    e.stopPropagation();
    this.tabService.activeTab(tabItem.tab);
  }

  closeTab(tabItem, e) {
    e.stopPropagation();
    this.tabService.removeTab(tabItem.tab);
    this.setTabItems();
  }

  public scrollToEnd(): void {
    if (this.viewContainerRef) {
      const target = this.viewContainerRef
        .map((x) => {
          if (x.element.nativeElement.classList.contains('active'))
            return x.element.nativeElement;
        })
        .filter(Boolean)[0];
      if (target) {
        this.componentRef.directiveRef.scrollTo(
          target.offsetLeft - 100,
          0,
          500
        );
      }
      1;
    }
  }
}
