import {
  Component,
  AfterContentInit,
  AfterContentChecked,
  ContentChildren,
  QueryList,
  Input,
  ComponentFactoryResolver,
} from '@angular/core';
import { TabItemComponent } from './../tab-item/tab-item.component';
import { Observable } from 'rxjs';
import { startWith, map, delay } from 'rxjs/operators';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';

@Component({
  selector: 'app-tabs',
  templateUrl: './tabs.component.html',
  styleUrls: [],
})
export class TabsComponent implements AfterContentInit, AfterContentChecked {
  @Input() fullWidth: boolean;
  @Input() public set activeTabIndex(value: number) {
    this._activeTabIndex = value || 0;
    try {
      const items = this.tabs.toArray();
      this.selectTab(items[this._activeTabIndex]);
    } catch (e) {}
  }

  @ContentChildren(TabItemComponent) tabs: QueryList<TabItemComponent>;
  tabItems$: Observable<TabItemComponent[]>;
  activeTab: TabItemComponent;
  _activeTabIndex: number;

  public config: PerfectScrollbarConfigInterface = {
    suppressScrollX: true,
    suppressScrollY: true,
  };

  constructor() {}

  ngAfterContentInit(): void {
    this.tabItems$ = this.tabs.changes
      .pipe(startWith(''))
      .pipe(delay(0))
      .pipe(map(() => this.tabs.toArray()));
  }

  ngAfterContentChecked() {
    if (!this.activeTab) {
      Promise.resolve().then(() => {
        this.activeTab = this.tabs.first;
      });
    }
  }

  selectTab(tabItem: TabItemComponent) {
    if (this.activeTab === tabItem || !tabItem) {
      return;
    }
    if (this.activeTab) {
      this.activeTab.isActive = false;
    }
    this.activeTab = tabItem;
    tabItem.isActive = true;
  }
}
