import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { DeviceQueryOptions } from 'app/shared/definitions';
import { DataService } from 'app/shared/services/data.service';
import * as moment from 'moment';
import { map, tap } from 'rxjs/operators';
import { NotificationService } from '../../../shared/services/notification.service';
import {
  DataHistory,
  DeviceRichInformation,
  Response,
} from '../../../../../ipc-helpers/definitions';
import { AmChartComponent } from '../am-chart/am-chart.component';
import { ColumnMode } from '@swimlane/ngx-datatable';
import { translations } from 'app/locale/translations';
import { NavigatorService } from 'app/shared/services/navigator.service';

export function extractColumnsFromDevice(device: DeviceRichInformation) {
  return Object.keys(device.dataHistory[0].data);
}

@Component({
  selector: 'app-device-view',
  templateUrl: './device-view.component.html',
  styleUrls: ['./device-view.component.scss'],
})
export class DeviceViewComponent implements OnInit, OnDestroy {
  public $device: DeviceRichInformation;
  public selectedSubData: string = '';
  public res: Response<DeviceRichInformation>;

  @Input() public params: { id?: number } = {};

  public historyView: DataHistory[] = [];
  public pagination = {
    offset: 0,
    limit: 20,
  };

  public get beginYear(): number {
    return new Date(
      this.$device.firstMessage.message_original_creation
    ).getFullYear();
  }

  public get endYear(): number {
    return new Date(
      this.$device.lastMessage.message_original_creation
    ).getFullYear();
  }

  public date_range = new FormControl();

  @Input() public set device(device: DeviceRichInformation) {
    this.$device = device;

    this.historyView = (this.$device.dataHistory || []).map((item) => {
      return {
        ...item,
        message_original_creation: moment(
          item.message_original_creation
        ).format('YYYY-MM-DD HH:mm'),
      };
    });
    if (this.$device.dataHistory) {
      this.tableColumns = extractColumnsFromDevice(this.$device);
    }
  }

  ColumnMode = ColumnMode;
  public get device(): DeviceRichInformation {
    return this.$device;
  }

  @ViewChild('chartView', { static: false })
  public chartView: AmChartComponent;

  private subscriptions = [];

  constructor(
    private dataService: DataService,
    private notificationService: NotificationService,
    public navigatorService: NavigatorService
  ) {
    this.isInvalidDate = this.isInvalidDate.bind(this);
  }

  public tableColumns: string[];

  ngOnInit(): void {
    this.getDeviceHistory();

    this.date_range.valueChanges
      .pipe(
        tap(() => {
          this.getDeviceHistory();
        })
      )
      .subscribe();
  }

  private buildDeviceQuery(): DeviceQueryOptions {
    const date = this.date_range.value || { startDate: null, endDate: null };

    const options: DeviceQueryOptions = {
      start: moment(date.startDate).toISOString(),
      end: moment(date.endDate).toISOString(),
      limit: this.pagination.limit,
      offset: this.pagination.offset,
    };

    if (!options.start) {
      delete options.start;
    }

    if (!options.end) {
      delete options.end;
    }

    return options;
  }

  public setPage({ offset, pageSize }) {
    this.pagination = {
      limit: pageSize,
      offset: offset * pageSize,
    };

    this.getDeviceHistory();
  }

  private getDeviceHistory() {
    const id = this.params.id;

    this.dataService
      .getDevice(id, this.buildDeviceQuery())
      .pipe(
        map((res) => {
          this.res = res;
          return res.data as any;
        }),
        tap((device: DeviceRichInformation) => {
          if (!device) {
            this.notificationService.showFailedMessage(
              translations.deviceView.noData,
              translations.deviceView.noDataForThisRange
            );
            return;
          }
          this.device = device;
          this.drawChartFromDataHistory(device);
        })
      )
      .subscribe();
  }

  drawChartFromDataHistory(
    device: DeviceRichInformation,
    columnIndex: number = 0
  ): void {
    if (
      !this.chartView ||
      !device.dataHistory ||
      device.dataHistory.length === 0
    ) {
      return;
    }

    this.chartView.reset();
    const columns: string[] = Object.keys(device.dataHistory[0].data);
    const column = columns[columnIndex];
    this.selectedSubData = column;

    const chartData = [];
    for (let item of device.dataHistory) {
      const value = item.data[column];
      chartData.push({
        date: item.message_original_creation,
        name: column,
        value: value && !isNaN(value) ? value : 0,
      });
    }

    this.chartView.addData(chartData);
  }

  public get minDate() {
    return moment(this.device.firstMessage.message_original_creation).startOf(
      'day'
    );
  }

  public get maxDate() {
    return moment(this.device.lastMessage.message_original_creation).endOf(
      'day'
    );
  }

  public isInvalidDate(date: moment.Moment): boolean {
    if (!this.device || !this.device.firstMessage) {
      return false;
    }

    if (date.isSameOrAfter(this.minDate) && date.isSameOrBefore(this.maxDate)) {
      return false;
    }

    return true;
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((sub) => sub && sub.unsubscribe());
  }
}
