import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { translations } from 'app/locale/translations';
import { ActivePort } from 'app/shared/definitions';
import { NotificationService } from 'app/shared/services/notification.service';
import { IResponse } from 'response-type';
import { Subscription } from 'rxjs';
import { tap } from 'rxjs/operators';
import { DataService } from '../../../shared/services/data.service';

@Component({
  selector: 'app-open-port',
  templateUrl: './open-port.component.html',
  styleUrls: ['./open-port.component.scss'],
})
export class OpenPortComponent implements OnInit, OnDestroy {
  private subscriptions: Subscription[] = [];
  public res: IResponse<ActivePort>;

  public AvailablePorts: Array<{ value: any; label: any }> = [
    {
      value: 'serialport',
      label: 'Serial Port',
    },
    {
      value: 'udp',
      label: 'UDP',
    },
  ];

  public form = new FormGroup({
    type: new FormControl('serialport'),
    port: new FormControl(23000),
    path: new FormControl(''),
    name: new FormControl(''),
    baudRate: new FormControl(''),
  });

  public lines = [];

  constructor(
    private dataService: DataService,
    private notification: NotificationService
  ) {}

  ngOnInit(): void {}

  submit(): void {
    this.dataService
      .postActivePort(this.form.value)
      .pipe(
        tap((res) => {
          this.res = res;
          if (res.error) {
            this.notification.showFailedMessage(
              translations.openPort.errorTitle,
              res.error.message || translations.openPort.errorDescription
            );
          } else if (res.data) {
            this.notification.showSuccessMessage(
              translations.openPort.openSuccessTitle,
              translations.openPort.openSuccessDescription
            );
          }
        })
      )
      .subscribe();
  }

  ngOnDestroy(): void {
    this.subscriptions
      .filter(Boolean)
      .forEach((subscription) => subscription.unsubscribe());
  }
}
