import { Component } from '@angular/core';
import { NavigatorService } from 'app/shared/services/navigator.service';

@Component({
  selector: 'app-welcome-tab',
  templateUrl: './welcome-tab.component.html',
  styleUrls: ['./welcome-tab.component.scss'],
})
export class WelcomeTabComponent {
  constructor(private navigatorService: NavigatorService) {}

  openCSVImport(): void {
    this.navigatorService.openImportCSVTab();
  }

  openDevicesTab(): void {
    this.navigatorService.openDevices();
  }

  goToLink(url: string) {
    window.open(url, '_blank');
  }
}
