import { Component, Inject, NgZone, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { random } from 'lodash';

import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import { SimpleTimeNumericChartEntry } from 'app/shared/definitions';
import { timer } from 'rxjs';

@Component({
  selector: 'app-am-chart',
  templateUrl: './am-chart.component.html',
  styleUrls: ['./am-chart.component.scss'],
})
export class AmChartComponent {
  private chart: am4charts.XYChart;
  public divId = `doc-${random(111111, 9999999)}`;

  constructor(@Inject(PLATFORM_ID) private platformId, private zone: NgZone) {}

  // Run the function only in the browser
  browserOnly(f: () => void) {
    if (isPlatformBrowser(this.platformId)) {
      this.zone.runOutsideAngular(() => {
        f();
      });
    }
  }

  ngAfterViewInit() {
    timer(100).subscribe(() => {
      this.drawChart();
    });
  }

  private drawChart(): void {
    this.browserOnly(() => {
      am4core.useTheme(am4themes_animated);

      let chart = am4core.create(this.divId, am4charts.XYChart);

      chart.paddingRight = 20;

      let data = [];

      chart.data = data;

      let dateAxis = chart.xAxes.push(new am4charts.DateAxis());
      dateAxis.renderer.grid.template.location = 0;

      let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
      valueAxis.tooltip.disabled = true;
      valueAxis.renderer.minWidth = 35;

      let series = chart.series.push(new am4charts.LineSeries());
      series.dataFields.dateX = 'date';
      series.dataFields.valueY = 'value';
      series.tooltipText = '{valueY.value}';

      series.strokeWidth = 0;
      series.fillOpacity = 0.5;
      series.tensionX = 0.77;
      series.fill = am4core.color('blue');

      chart.cursor = new am4charts.XYCursor();

      this.chart = chart;
    });
  }

  ngOnDestroy() {
    this.browserOnly(() => {
      if (this.chart) {
        this.chart.dispose();
      }
    });
  }

  addData(data: SimpleTimeNumericChartEntry[]): void {
    if (!this.chart) {
      return;
    }

    this.chart.addData(data, this.chart.data.length > 50 ? data.length : 0);
  }

  reset(): void {
    this.chart.removeData(this.chart.data.length);
  }
}
