import { evaluate } from 'mathjs';

export abstract class DataBlender<T, V> {
  abstract name: string;
  abstract blend(input: T, options: any): V;
}

class ExtractNumberBlender extends DataBlender<string, number[]> {
  name = 'ExtractNumberBlender';
  blend(input: string, options: any): number[] {
    return input.match(/[-+]?\d+(\.\d+)?/g).map((t) => +t);
  }
}

class DigitalSignalBlender extends DataBlender<string, number> {
  name = 'DigitalSignalBlender';
  blend(input: string, options: any): number {
    if (!input) {
      return null;
    }

    if (['on', 'high', '1'].includes(input.trim().toLowerCase())) {
      return 1;
    }

    if (['off', 'low', '0'].includes(input.trim().toLowerCase())) {
      return 0;
    }

    return null;
  }
}

class TakeFirstBlender extends DataBlender<any[], any> {
  name = 'TakeFirstBlender';
  blend(input: any[], options: any): any {
    return input ? input[0] : null;
  }
}

class DoubleBlender extends DataBlender<number, number> {
  name = 'DoubleBlender';
  blend(input: number, options: any): any {
    return input * 2;
  }
}

class MathBlender extends DataBlender<number, number> {
  name = 'MathBlender';
  blend(input: number, options: any): number {
    return evaluate(options.expression, { input });
  }
}

class JsonBlender extends DataBlender<string, object> {
  name = 'JsonBlender';
  blend(input: string, options: any): object {
    let data = {};
    try {
      data = JSON.parse(input);
    } catch (error) {}

    return data;
  }
}

export function applyBlend(input: any, sequence: DataBlender<any, any>[]): any {
  let output = input;
  for (const cls of sequence) {
    output = cls.blend(output, { expression: 'input * 100' });
  }

  return output;
}

export const BuiltinDataBlenders = {
  ExtractNumberBlender,
  TakeFirstBlender,
  DoubleBlender,
  MathBlender,
  DigitalSignalBlender,
  JsonBlender,
};
