import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { DatabaseConfiguration } from 'app/shared/definitions';
import { NotificationService } from 'app/shared/services/notification.service';
import { IResponse } from 'response-type';
import { Subscription } from 'rxjs';
import { tap } from 'rxjs/operators';
import { translations } from '../../../locale/translations';
import { DataService } from '../../../shared/services/data.service';

@Component({
  selector: 'app-database-configuration',
  templateUrl: './database-configuration.component.html',
  styleUrls: ['./database-configuration.component.scss'],
})
export class DatabaseConfigurationComponent implements OnInit, OnDestroy {
  private subscriptions: Subscription[] = [];
  public res: IResponse<DatabaseConfiguration>;
  public AvailableVendors: Array<{ value: any; label: any }> = [
    {
      value: 'mysql',
      label: 'MySQL',
    },
    {
      value: 'sqlite',
      label: 'SQLite',
    },
  ];
  public form = new FormGroup({
    vendor: new FormControl(''),
    username: new FormControl(''),
    password: new FormControl(''),
    database: new FormControl(''),
  });

  public lines = [];

  constructor(
    private dataService: DataService,
    private notification: NotificationService
  ) {}

  ngOnInit(): void {
    this.dataService
      .getDataBaseInformation()
      .pipe(
        tap((data: DatabaseConfiguration) => {
          this.form.patchValue(data);
        })
      )
      .subscribe();
  }

  submit(): void {
    this.dataService
      .postDatabaseConfiguration(this.form.value)
      .pipe(
        tap((res) => {
          this.res = res;
          if (res.error) {
            this.notification.showFailedMessage(null, res.error.message);
          } else if (res.data) {
            this.notification.showSuccessMessage(
              translations.general.success,
              translations.database.configurationUpdated
            );
          }
        })
      )
      .subscribe();
  }

  ngOnDestroy(): void {
    this.subscriptions
      .filter(Boolean)
      .forEach((subscription) => subscription.unsubscribe());
  }
}
