import { Component, Input, OnInit } from '@angular/core';
import { DataService } from 'app/shared/services/data.service';
import {
  width,
  cellSize,
  height,
  week,
  week_days,
  month,
  day,
  format,
  color,
} from './heatmap.helper';

@Component({
  selector: 'app-heatmap',
  templateUrl: './heatmap.component.html',
  styleUrls: ['./heatmap.component.scss'],
})
export class HeatmapComponent implements OnInit {
  @Input() public deviceId = 0;
  @Input() public beginYear: number;
  @Input() public endYear: number;

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    this.dataService.deviceYearDayStat(this.deviceId).subscribe((res) => {
      this.initData(
        this.beginYear,
        this.endYear + 1,
        res.data.items.map((t) => {
          return {
            count: t.count,
            date: parseInt(t.date.replace(/-/g, ''), 10),
          };
        })
      );
    });
  }

  initData(
    beginYear: number,
    lastYear: number,
    json: Array<{ count: number; date: number }>
  ): void {
    var svg = d3
      .select('.calender-map')
      .selectAll('svg')
      .data(d3.range(beginYear, lastYear))
      .enter()
      .append('svg')
      .attr('width', '100%')
      .attr('data-height', '0.5678')
      .attr('viewBox', '0 0 900 105')
      .attr('class', 'RdYlGn')
      .append('g')
      .attr(
        'transform',
        'translate(' +
          (width - cellSize * 53) / 2 +
          ',' +
          (height - cellSize * 7 - 1) +
          ')'
      );

    svg
      .append('text')
      .attr('transform', 'translate(-38,' + cellSize * 3.5 + ')rotate(-90)')
      .style('text-anchor', 'middle')
      .text(function (d) {
        return d;
      });

    for (var i = 0; i < 7; i++) {
      svg
        .append('text')
        .attr('transform', 'translate(-5,' + cellSize * (i + 1) + ')')
        .style('text-anchor', 'end')
        .attr('dy', '-.25em')
        .text(function (d) {
          return week_days[i];
        });
    }

    var rect = svg
      .selectAll('.day')
      .data(function (d) {
        return d3.time.days(new Date(d, 0, 1), new Date(d + 1, 0, 1));
      })
      .enter()
      .append('rect')
      .attr('class', 'day')
      .attr('width', cellSize)
      .attr('height', cellSize)
      .attr('x', function (d) {
        return week(d) * cellSize;
      })
      .attr('y', function (d) {
        return day(d) * cellSize;
      })
      .attr('fill', '#fff')
      .datum(format);

    var legend = svg
      .selectAll('.legend')
      .data(month)
      .enter()
      .append('g')
      .attr('class', 'legend')
      .attr('transform', function (d, i) {
        return 'translate(' + ((i + 1) * 50 + 8) + ',0)';
      });

    legend
      .append('text')
      .attr('class', function (d, i) {
        return month[i];
      })
      .style('text-anchor', 'end')
      .attr('dy', '-.25em')
      .text(function (d, i) {
        return month[i];
      });

    svg
      .selectAll('.month')
      .data(function (d) {
        return d3.time.months(new Date(d, 0, 1), new Date(d + 1, 0, 1));
      })
      .enter()
      .append('path')
      .attr('class', 'month')
      .attr('id', function (d, i) {
        return month[i];
      })
      .attr('d', monthPath);

    var count_Max = d3.max(json, function (d) {
      return d.count;
    });

    var data = d3
      .nest()
      .key(function (d) {
        return d.date;
      })
      .rollup(function (d) {
        return Math.sqrt(d[0].count / count_Max);
      })
      .map(json);

    rect
      .filter(function (d) {
        return d in data;
      })
      .attr('fill', function (d) {
        return color(data[d]);
      })
      .attr('data-title', function (d) {
        return 'value : ' + Math.round(data[d] * 100);
      });
  }
}

function monthPath(t0) {
  var t1 = new Date(t0.getFullYear(), t0.getMonth() + 1, 0),
    d0 = +day(t0),
    w0 = +week(t0),
    d1 = +day(t1),
    w1 = +week(t1);
  return (
    'M' +
    (w0 + 1) * cellSize +
    ',' +
    d0 * cellSize +
    'H' +
    w0 * cellSize +
    'V' +
    7 * cellSize +
    'H' +
    w1 * cellSize +
    'V' +
    (d1 + 1) * cellSize +
    'H' +
    (w1 + 1) * cellSize +
    'V' +
    0 +
    'H' +
    (w0 + 1) * cellSize +
    'Z'
  );
}

declare var d3: any;
