import { Component, OnInit, ViewChild } from '@angular/core';
import { DataService } from 'app/shared/services/data.service';
import { interval, timer } from 'rxjs';
import { AmChartComponent } from '../am-chart/am-chart.component';
import { GaugeChartComponent } from '../gauge-chart/gauge-chart.component';
import { ChartRange } from '../../../shared/definitions';

@Component({
  selector: 'app-realtime-messages-chart',
  templateUrl: './realtime-messages-chart.component.html',
  styleUrls: ['./realtime-messages-chart.component.scss'],
})
export class RealtimeMessagesChartComponent implements OnInit {
  @ViewChild('chartView', { static: false })
  public chartView: AmChartComponent;
  @ViewChild('gaugeChartView', { static: false })
  public gaugeChartView: GaugeChartComponent;
  public current = 0;

  public gaugeRange: ChartRange[] = [
    {
      value: 0,
      endValue: 50,
    },
    {
      value: 50,
      endValue: 70,
    },
    {
      value: 70,
      endValue: 100,
    },
  ];

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    let count = 0;

    this.dataService.realtimeMessage$().subscribe(() => {
      count++;
    });

    interval(1000).subscribe(() => {
      this.chartView.addData([
        {
          date: new Date(),
          name: 'data',
          value: count,
        },
      ]);

      this.gaugeChartView.addData(this.current);

      this.current = count;
      count = 0;
    });
  }
}
