import { Component, OnInit } from '@angular/core';
import { DataService } from 'app/shared/services/data.service';
import { ModalService } from 'app/shared/services/modal.service';
import {
  state,
  style,
  animate,
  transition,
  trigger,
} from '@angular/animations';
import { ActiveStreamsComponent } from '../active-streams/active-streams.component';
import { StatusbarService } from '../../../shared/services/statusbar.service';
import { StatusbarStatus } from '../../../shared/definitions';
import { NavigatorService } from 'app/shared/services/navigator.service';

@Component({
  selector: 'app-connection-status',
  templateUrl: './connection-status.component.html',
  styleUrls: [],
  animations: [
    trigger('showHide', [
      state(
        'hide',
        style({
          opacity: 0,
        })
      ),
      state(
        'show',
        style({
          opacity: 1,
        })
      ),
      transition('hide => show', [animate('0.3s')]),
      transition('show => hide', [animate('0.3s')]),
    ]),
  ],
})
export class ConnectionStatusComponent implements OnInit {
  public connected: boolean = null;
  public content: string = 'Checking...';
  constructor(
    private dataService: DataService,
    private modalService: ModalService,
    private statusbarService: StatusbarService,
    public navigatorService: NavigatorService
  ) {}

  public animationState: 'enter' | 'leave' = 'enter';

  ngOnInit(): void {
    this.dataService.bridgeConnected$().subscribe((status: boolean) => {
      if (this.connected === status) {
        return;
      }

      this.connected = status;
      this.statusbarService.setStatus(
        status ? StatusbarStatus.NORMAL : StatusbarStatus.WARNING
      );
      this.changeConnectionState(this.connected);
    });
  }

  private changeConnectionState(connected: boolean): void {
    this.animationState = 'leave';
    setTimeout(() => {
      if (connected) {
        this.content = 'Connected';
      }
      if (!connected) {
        this.content = 'Offline';
      }

      this.animationState = 'enter';
    }, 300);
  }

  public showNetworkStatus() {
    this.modalService.open({
      title: 'Your network',
      content: ActiveStreamsComponent,
    });
  }

  public get wifiIcon(): string {
    return this.content == 'Checking...'
      ? 'icon-network_check'
      : this.connected
      ? 'icon-wifi'
      : 'icon-wifi_off';
  }
}
