import { sortBy } from 'lodash';

// Represents the message which is coming from device
export class Device {
  imei?: string;
  id?: number;
  data?: any;

  set(data: Device): Device {
    Object.assign(this, data);
    return this;
  }
}

export interface DataHistory {
  deviceId: number;
  ref_key: number;
  message_original_creation: string | Date;
  data: any;
}

// When data is populated into a device and we have enough information
export class DeviceRichInformation extends Device {
  dataHistory: Array<DataHistory>;
  dataInsights: DeviceInsightInformation;
  firstMessage: DataHistory;
  name?: string;
  lastMessage: DataHistory;

  from(data: DeviceRichInformation): DeviceRichInformation {
    if (data.dataHistory && data.dataHistory.map) {
      data.dataHistory = data.dataHistory
        .filter((item) => {
          if (Object.values(item.data).filter((t) => t !== null).length === 0) {
            return false;
          }

          return true;
        })
        .map((item) => {
          return {
            ...item,
            message_original_creation: new Date(item.message_original_creation),
          };
        });

      data.dataHistory = sortBy(data.dataHistory, 'message_original_creation');
    }

    Object.assign(this, data);
    return this;
  }
}

export class DeviceInsightInformation {
  totalMessages?: number;
  totalSubMessages?: number;
}

