export class ResponseErrorItem {
  domain?: string;
  reason?: string;
  message?: string;
  location?: string;
  locationType?: string;
  extendedHelp?: string;
  sendReport?: string;
}

export class ResponseError {
  code?: number;
  message: string;
  errors?: Array<ResponseErrorItem>;
}

export class ResponseData<T> {
  kind?: string;
  fields?: string;
  etag?: string;
  id?: string;
  lang?: string;
  updated?: string;
  deleted?: boolean;
  currentItemCount?: number;
  itemsPerPage?: number;
  startIndex?: number;
  totalItems?: number;
  pageIndex?: number;
  totalPages?: number;
  items?: Array<T>;
  [key: string]: any;
}

export class Response<T> {
  apiVersion?: string;
  context?: string;
  id?: string;
  params?: {
    id?: string;
  };
  data?: ResponseData<T>;
  error?: ResponseError;
}

export function successResponse<T>(data: any): Response<T> {
  const res = new Response<T>();

  if (data && data.push) {
    res.data = { items: data };
  } else {
    res.data = {
      ...data,
      fields: Object.keys('data'),
    };
  }

  return res;
}

