import { Component, OnInit, ViewChild } from '@angular/core';
import {
  ChartRange,
  TemperatureDataList,
  ModalDialogType,
  TemperatureOverview,
  TemperatureOverviewPercentageType,
  TemperatureOverviewType,
} from '../../../shared/definitions';
import { BarChartComponent } from '../bar-chart/bar-chart.component';
import { interval } from 'rxjs';
import { ColumnMode, SelectionType } from '@swimlane/ngx-datatable';
import * as moment from 'moment';
import { GaugeChartComponent } from '../gauge-chart/gauge-chart.component';
import { LineChartComponent } from '../line-chart/line-chart.component';
import { ModalService } from '../../../shared/services/modal.service';
import { DeviceSingleComponent } from '../device-single/device-single.component';

@Component({
  selector: 'app-temperature-view',
  templateUrl: './temperature-view.component.html',
  styleUrls: ['./temperature-view.component.scss'],
})
export class TemperatureViewComponent implements OnInit {
  @ViewChild('chartView', { static: false })
  public chartView: BarChartComponent;
  @ViewChild('chartAverageView', { static: false })
  public chartAverageView: LineChartComponent;
  @ViewChild('gaugeChartView', { static: false })
  public gaugeChartView: GaugeChartComponent;
  public temperatureDataList: TemperatureDataList[] = [];
  public temperatureOverview: TemperatureOverview[] = [];
  public isLive: Boolean = true;
  public chartRange: ChartRange[] = [
    {
      value: 0,
      endValue: 20,
      color: '#f14b50',
    },
    {
      value: 20,
      endValue: 40,
      color: '#fdbe47',
    },
    {
      value: 40,
      endValue: 60,
      color: '#bfda5e',
    },
    {
      value: 60,
      endValue: 80,
      color: '#76c76b',
    },
    {
      value: 80,
      endValue: 100,
      color: '#3fab6b',
    },
  ];

  public page = {
    count: 10,
    offset: 0,
    limit: 10,
  };

  ColumnMode = ColumnMode;
  SelectionType = SelectionType;

  constructor(private modalService: ModalService) {}

  setLive(status: Boolean) {
    this.isLive = status;
  }

  onActivate(event) {
    event.type === 'click' && event.cellElement && event.cellElement.blur();
  }

  public editDevice() {
    this.modalService.open({
      title: 'Running Tasks',
      content: DeviceSingleComponent,
      type: ModalDialogType.NOTIFY,
    });
  }

  ngOnInit(): void {
    let current = 0,
      counter = 0;
    interval(1000).subscribe(() => {
      counter++;
      current = Math.round(Math.random() * 100);
      this.chartView.addData([
        {
          date: new Date(),
          name: 'data',
          value: current,
        },
      ]);

      this.chartAverageView.addData([
        {
          date: new Date(),
          name: 'data',
          value: current,
        },
      ]);

      this.page = {
        count: counter,
        offset: 1,
        limit: 10,
      };

      this.temperatureOverview = [
        TemperatureOverviewType.MIN,
        TemperatureOverviewType.MAX,
        TemperatureOverviewType.AVG,
      ].map((i) => ({
        value: '' + (Math.random() * 20).toFixed(2),
        percentage: '' + (Math.random() * 5).toFixed(2),
        percentageType:
          Math.random() < 0.5
            ? TemperatureOverviewPercentageType.POS
            : TemperatureOverviewPercentageType.NEG,
        type: i,
      }));

      this.temperatureDataList = [
        ...this.temperatureDataList,
        {
          id: counter,
          value: Math.round(Math.random() * 10) + 'C',
          date: moment(new Date()).format('YYYY-MM-DD HH:mm'),
        },
      ];
      this.gaugeChartView.addData(current);
    });
  }
}
