import { Injectable } from '@angular/core';
import { ImportJobs, ImportJobsStatus } from '../definitions';
import { DataService } from 'app/shared/services/data.service';
import { ProgressBarService } from './progress-bar.service';
import { numberWithCommas } from '../utils';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class ImportService {
  public importItems = [];
  public lastItems = [];
  public jobs: BehaviorSubject<Array<ImportJobs>> = new BehaviorSubject([]);

  constructor(
    private dataService: DataService,
    private progressBarService: ProgressBarService
  ) {
    const jobs = this.jobs;
    this.dataService.importStatus$().subscribe((items) => {
      this.importItems = items;
      this.addToStore();
      this.checkImporingFinished();
      if (!(this.importItems && this.importItems.length > 0)) {
        return;
      }
      this.lastItems = this.importItems;
      for (let item of this.importItems) {
        this.progressBarService.addProgress({
          id: item.jobId,
          title:
            'Importing ' +
            numberWithCommas(item.linesProccessed) +
            ' of ' +
            numberWithCommas(item.totalLines),
          precentage: Math.round(
            (item.linesProccessed * 100) / item.totalLines
          ),
          data: item,
          stopFunction() {
            dataService.importStop(item.jobId).subscribe((result) => {
              const newJobs = jobs
                .getValue()
                .map((e) =>
                  e.jobId === item.jobId
                    ? { ...e, status: ImportJobsStatus.CANCELED }
                    : e
                );
              jobs.next(newJobs);
              progressBarService.removeProgress(item.jobId);
            });
          },
        });
      }
    });
  }

  addToStore() {
    let allJobs = [];
    for (let item of this.importItems) {
      allJobs = this.jobs.getValue() || [];
      if (allJobs.find((x) => x.jobId == item.jobId)) {
        const jobsItems = allJobs.map((e) =>
          e.jobId === item.jobId && e.status === ImportJobsStatus.ACTIVE
            ? {
                ...e,
                linesProccessed: item.linesProccessed,
                secondsLeft: item.secondsLeft,
                timeLeft: item.timeLeft,
              }
            : e
        );
        this.jobs.next(jobsItems);
      } else {
        const itemStore: ImportJobs = {
          jobId: item.jobId,
          filename: item.filename,
          linesProccessed: item.linesProccessed,
          secondsLeft: item.secondsLeft,
          timeLeft: item.timeLeft,
          totalLines: item.totalLines,
          status: ImportJobsStatus.ACTIVE,
        };
        this.jobs.next([...allJobs, itemStore]);
      }
    }
  }

  checkImporingFinished() {
    if (this.lastItems.length === this.importItems.length) {
      return;
    }
    for (let lastItem of this.lastItems) {
      if (
        this.importItems.findIndex((x) => x.jobId === lastItem.jobId) === -1 &&
        this.jobs
          .getValue()
          .find(
            (x) =>
              x.jobId == lastItem.jobId && x.status === ImportJobsStatus.ACTIVE
          )
      ) {
        const jobsItems = this.jobs
          .getValue()
          .map((e) =>
            e.jobId === lastItem.jobId
              ? { ...e, status: ImportJobsStatus.FINISHED }
              : e
          );
        this.jobs.next(jobsItems);
        this.progressBarService.removeProgress(lastItem.jobId);
        this.lastItems = this.lastItems.filter(
          (x) => x.jobId !== lastItem.jobId
        );
      }
    }
  }
}
