import { Component, OnInit } from '@angular/core';
import { DataVisualisionElement } from 'app/shared/definitions';
import { DataService } from 'app/shared/services/data.service';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-select-visualization-list',
  templateUrl: './select-visualization-list.component.html',
  styleUrls: [],
})
export class SelectVisualiztionComponent implements OnInit {
  public items: DataVisualisionElement[] = [];
  public data: BehaviorSubject<DataVisualisionElement> = new BehaviorSubject(
    null
  );

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    this.dataService.availableVisualisionTypes$().subscribe((items) => {
      this.items = items;

      const enabledItems = this.items.filter(
        (item: DataVisualisionElement) => !item.disabled
      );
      if (enabledItems.length === 1) {
        this.data.next(enabledItems[0]);
      }
    });
  }

  public selectItem(item: DataVisualisionElement) {
    if (item.disabled) {
      return;
    }

    this.data.next(item);
  }
}
