/**
 * This service provides all information that application needs to work.
 * It means any http request, data collection, etc should go through this service.
 * Do not split it into module layer. It's not needed and intentionally all are placed in
 * the same place.
 */
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import {
  DeviceLocation,
  DeviceRichInformation,
  IncomingMessageFromDevice,
  NetworkConnection,
  Response,
} from '../../../../ipc-helpers/definitions';
import {
  ActivePort,
  ActivePortSubscription,
  BarChartVisualision,
  CSVImportOptions,
  DatabaseConfiguration,
  DataTableVisualision,
  DataVisualisionElement,
  DeviceQueryOptions,
  GaugeChartVisualision,
  IconReferences,
  LineChartVisualision,
  MapVisualision,
  PieChartVisualision,
  RelayControllerVisualision,
  ReportScreen,
  ScatterPlotChartVisualision,
  SerialMonitorVisualision,
  SerialPort,
} from '../definitions';
import { API } from './common.functions';
import { RealtimeService } from './realtime.service';
import { StoreService } from './store.service';

@Injectable({ providedIn: 'root' })
export class DataService {
  constructor(
    private http: HttpClient,
    private realtimeService: RealtimeService,
    private storeService: StoreService
  ) {}

  /**
   * Returns the host ips connected to different parts of the app. Useful for showing user
   * that which ips user can connect to.
   */
  public hostNetworkInformation$(): Observable<NetworkConnection[]> {
    return this.storeService.networkInformation.asObservable();
  }

  public bridgeConnected$(): Observable<boolean> {
    return this.storeService.bridgeConnected.asObservable();
  }

  /**
   * Returns devices latest information which is stored into the database
   * try to not use this function as much possible, because devices$() always
   * give you this information with some delay.
   */
  public getDevices(
    offset: number = 0,
    limit: number = 10
  ): Observable<Response<DeviceRichInformation>> {
    return this.http
      .get<Response<DeviceRichInformation>>(
        API(`api/iot/devices?offset=${offset}&limit=${limit}`)
      )
      .pipe(
        map((res) => {
          res.data.items = res.data.items.map((t) =>
            new DeviceRichInformation().from(t)
          );
          this.storeService.devicesList.next(res.data.items);
          return res;
        })
      );
  }

  public devices$(): Observable<DeviceRichInformation[]> {
    return new Observable((observer) => {
      this.getDevices().subscribe();
      this.storeService.devicesList.subscribe((items) => observer.next(items));
    });
  }

  public getSerialPorts(): Observable<Response<SerialPort>> {
    return this.http.get<Response<SerialPort>>(API('api/iot/serialports')).pipe(
      tap((res) => {
        this.storeService.serialPorts.next(res.data.items);
      })
    );
  }

  public killStream(id: string): Observable<Response<any>> {
    return this.http.delete<Response<SerialPort>>(API('api/iot/stream/' + id));
  }

  public observeSerialPort(path: string): Observable<Response<SerialPort>> {
    return this.http.post<Response<SerialPort>>(
      API('api/iot/observe-serialport'),
      {
        path,
      }
    );
  }

  public serialPorts$(): Observable<SerialPort[]> {
    return new Observable((observer) => {
      this.getSerialPorts().subscribe();
      this.storeService.serialPorts.subscribe((items) => observer.next(items));
    });
  }

  public getDevice(
    id: number,
    options: DeviceQueryOptions = {}
  ): Observable<Response<DeviceRichInformation>> {
    return this.http
      .get<Response<DeviceRichInformation>>(API('api/iot/device/' + id), {
        params: options as any,
      })
      .pipe(
        map((res) => {
          return res;
        })
      );
  }

  /**
   * Mirrors the incoming data from every devices to dashboard which goes to iot
   * server before processing. Note this is raw data incoming from device, it is not
   * validated yet, and cannot be used where DeviceRichInformation is needed.
   * Use only, when delay is unacceptable for data. (Such as keys)
   */
  public realtimeMessage$(): Observable<IncomingMessageFromDevice<any>> {
    return this.realtimeService.lastMessageReceived;
  }

  private getActivePortStreams() {
    return this.http.get<Response<any>>(API('api/iot/streams')).pipe(
      map((res) => res.data.items),
      tap((items) => {
        this.storeService.activeStreams.next(items);
      })
    );
  }

  public getActivePorts() {
    return this.http
      .get<Response<ActivePort>>(API('api/iot/activeports'))
      .pipe(map((res) => res.data.items));
  }

  public getServerInformation() {
    return this.http.get<Response<any>>(API('')).pipe(map((res) => res.data));
  }

  public activeStreams$(): Observable<ActivePortSubscription[]> {
    return new Observable((observer) => {
      this.getActivePortStreams().subscribe();
      this.storeService.activeStreams.subscribe((items) =>
        observer.next(items)
      );
    });
  }

  /**
   * Returns the locations in the app, defined by user.
   */
  public locations$(): Observable<DeviceLocation[]> {
    return of([{ id: 1, name: 'Default space' }] as DeviceLocation[]);
  }

  /**
   * Returns all available visualisation elements that user can put into their report screens.
   * Aka. Temperature, Controls, Serial Monitor
   */
  public availableVisualisionTypes$(): Observable<DataVisualisionElement[]> {
    return of([
      new BarChartVisualision(),
      new LineChartVisualision(),
      new SerialMonitorVisualision(),
      new GaugeChartVisualision(),
      new ScatterPlotChartVisualision(),
      new PieChartVisualision(),
      new DataTableVisualision(),
      new MapVisualision(),
      new RelayControllerVisualision(),
    ] as DataVisualisionElement[]);
  }

  /**
   * Users can build as many as dashboard they want, and place DataVisualisionElement objects
   * inside and connect them into the data.
   */
  public reportScreens$(): Observable<ReportScreen[]> {
    const item1 = new BarChartVisualision();
    item1.location = {
      x: 0,
      y: 1,
    };
    item1.name = 'Bar Chart 1';

    return of([
      {
        name: 'Dashboard 1',
        icon: IconReferences.BarChart,
        id: 1,
        elements: [item1],
      },
    ] as ReportScreen[]);
  }

  /**
   * Adds a new element into an specific sheet that user has.
   * Users can have as many as sheets they need, so pass the number here.
   */
  public createVisualElement(
    element: DataVisualisionElement,
    sheetId: number = 1
  ): Observable<any> {
    return this.http.post<Response<DeviceRichInformation>>(
      API('api/iot/visualization'),
      { ...element, sheetId }
    );
  }

  public postDevice(data: any): Observable<any> {
    return this.http.post<Response<DeviceRichInformation>>(
      API('api/iot/device'),
      { ...data }
    );
  }

  public importCSV(file, options: CSVImportOptions) {
    const formData: FormData = new FormData();
    formData.append('Image', file);
    formData.append('options', JSON.stringify(options));

    return this.http.post<Response<DeviceRichInformation>>(
      API('api/iot/import-csv'),
      formData
    );
  }

  public importStop(jobId: number) {
    return this.http.get<Response<any>>(API('api/iot/import-stop/' + jobId));
  }

  private getImportStatus() {
    return this.http.get<Response<any>>(API('api/iot/import-status')).pipe(
      tap((res) => {
        this.storeService.activeImports.next(res.data.items);
      })
    );
  }

  public importStatus$(): Observable<DeviceRichInformation[]> {
    return new Observable((observer) => {
      this.getImportStatus().subscribe();
      this.storeService.activeImports.subscribe((items) =>
        observer.next(items)
      );
    });
  }

  public serverGeneralInformation(): Observable<Response<any>> {
    return this.http.get<Response<any>>(API(''));
  }

  public postActivePort(data: ActivePort) {
    return this.http.post<Response<ActivePort>>(API('api/iot/openport'), data);
  }

  public deviceYearDayStat(deviceId: number) {
    return this.http.get<Response<any>>(
      API('api/iot/device-year-stats/' + deviceId)
    );
  }

  public triggerInternalIconTray() {
    return this.http.get<Response<ActivePort>>(API('tray/on'));
  }

  public getDataBaseInformation(): Observable<DatabaseConfiguration> {
    return this.http.get<any>(API('')).pipe(
      map((res: Response<any>) => {
        if (res.data.database && res.data.database.connectionOptions) {
          return res.data.database.connectionOptions;
        }

        return res;
      })
    );
  }

  public postDatabaseConfiguration(data: DatabaseConfiguration) {
    return this.http.post<Response<DatabaseConfiguration>>(
      API('api/iot/database'),
      data
    );
  }

  public deleteActivePort(id: number) {
    return this.http.delete<Response<ActivePort>>(
      API('api/iot/activeport/' + id)
    );
  }

  public deleteDevice(id: number) {
    return this.http.delete<Response<any>>(API('api/iot/device/' + id));
  }
}
