import { Component, HostListener, OnInit } from '@angular/core';
import { VisualizationService } from 'app/shared/services/visualization.service';
import { NavigatorService } from 'app/shared/services/navigator.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: [],
})
export class NavbarComponent implements OnInit {
  @HostListener('window:keyup', ['$event'])
  keyEvent(event: KeyboardEvent) {
    const $event: any = document.all ? window.event : event;
    if (/^(?:input|textarea|select|button)$/i.test($event.target.tagName)) {
      return;
    }

    if (event.key === 'n') {
      this.addButtonClick();
    }
  }

  constructor(
    private visualizationService: VisualizationService,
    private navigatorService: NavigatorService
  ) {}

  ngOnInit(): void {}

  /**
   * Use it for adding few elements
   */
  addButtonClick(): void {
    this.visualizationService.promptAddVisualization();
  }

  openDevicesTab(): void {
    this.navigatorService.openDevices();
  }

  openCSVImport(): void {
    this.navigatorService.openImportCSVTab();
  }

  openRealtimeTab(): void {
    this.navigatorService.openRealtimeTab();
  }

  openPortTab(): void {
    this.navigatorService.openPort();
  }

  openNewDevice(): void {
    this.navigatorService.openNewDevice();
  }

  openDatabaseConfig(): void {
    this.navigatorService.openDatabaseConfig();
  }

  openTemperatureView(): void {
    this.navigatorService.openTemperatureView();
  }

  logicBuilding(): void {
    this.navigatorService.openLogicBuilding();
  }

  public showSettings() {
    this.navigatorService.openSettings();
  }

  public listenToPort(): void {
    this.navigatorService.openPort();
  }
}
