import { Component, OnInit, forwardRef, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { FormBaseComponent } from '../FormBaseComponent';
import { DayPickerComponent } from '../day-picker/day-picker.component';
import { ModalService } from '../../../../shared/services/modal.service';
import {
  IsMobile,
  monthNames,
} from '../../../../shared/services/common.functions';

import * as moment from 'moment';

export const DEFAULT_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => FormDateComponent),
  multi: true,
};

function getNumberWithOrdinal(n: number): string {
  const s = ['th', 'st', 'nd', 'rd'];
  const v = n % 100;
  return n + (s[(v - 20) % 10] || s[v] || s[0]);
}

@Component({
  selector: 'app-form-date',
  templateUrl: './form-date.component.html',
  styleUrls: ['./form-date.component.scss'],
  providers: [DEFAULT_VALUE_ACCESSOR],
})
export class FormDateComponent extends FormBaseComponent implements OnInit {
  public isMobile = false;
  @Input() public type: 'date' | 'day' = 'date';
  @Input() public selectMonth = false;
  @Input() public minDate;
  @Input() public maxDate;

  constructor(private modal: ModalService) {
    super();
  }

  ngOnInit() {
    this.isMobile = IsMobile();
  }

  onNativeInput(value) {
    this.onChange({
      startDate: moment(value).toDate(),
      endDate: moment(value).toDate(),
    });
  }

  public showDate(value) {
    if (!value) {
      return '';
    }
    if (typeof value === 'string') {
      return value;
    }
    if (
      value.startDate &&
      value.endDate &&
      `${value.startDate}` === `${value.endDate}`
    ) {
      return moment(value.startDate).format('YYYY-MM-DD');
    }
    return `${moment(value.startDate).format('YYYY-MM-DD')} - ${moment(
      value.endDate
    ).format('YYYY-MM-DD')}`;
  }
  ranges: any = {
    Today: [moment(), moment()],
    Yesterday: [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
    'Last 7 Days': [moment().subtract(6, 'days'), moment()],
    'Last 30 Days': [moment().subtract(29, 'days'), moment()],
    'This Month': [moment().startOf('month'), moment().endOf('month')],
    'Last Month': [
      moment().subtract(1, 'month').startOf('month'),
      moment().subtract(1, 'month').endOf('month'),
    ],
  };

  @Input() isInvalidDate = (date) => {
    return false;
  };

  writeValue(value: any): void {
    if (this.isMobile && this.type === 'date') {
      this.value = moment(value.startDate).format('YYYY-MM-DD');
      return;
    }
    this.value = value;
  }

  public openSelectDateModal() {
    const value = this.value || { day: 1, month: 1 };

    this.modal
      .open({
        content: DayPickerComponent,
        title: 'Select date',
        params: {
          day: value.day,
          month: value.month,
          selectMonth: this.selectMonth,
        },
      })
      .subscribe((result) => {
        if (result.type === 'CONFIRMED') {
          this.writeValue(result.data);
          this.onInput(result.data);
        }
      });
  }

  public dayLabel(value) {
    if (this.selectMonth) {
      return `${monthNames[value.month - 1]} ${getNumberWithOrdinal(
        value.day
      )}`;
    }
    return getNumberWithOrdinal(value.day);
  }
}
