import {
  Component,
  OnInit,
  HostListener,
  ViewContainerRef,
  ViewChildren,
  QueryList,
} from '@angular/core';
import { MainTab, KEY_CODE } from '../../../shared/definitions';
import { TabService } from '../../../shared/services/tabs.service';

@Component({
  selector: 'app-main-tab',
  templateUrl: './main-tab.component.html',
  styleUrls: [],
})
export class MainTabComponent implements OnInit {
  @ViewChildren('tab', { read: ViewContainerRef }) viewContainerRef: QueryList<
    ViewContainerRef
  >;
  public tabs: MainTab[] = [];
  constructor(private tabService: TabService) {}

  @HostListener('window:keyup', ['$event'])
  keyEvent(event: KeyboardEvent) {
    const $event: any = document.all ? window.event : event;
    if (/^(?:input|textarea|select|button)$/i.test($event.target.tagName)) {
      return;
    }

    if (event.keyCode == KEY_CODE.RIGHT_ARROW) {
      this.tabService.nextTab();
    }
    if (event.keyCode == KEY_CODE.LEFT_ARROW) {
      this.tabService.prevTab();
    }

    if (event.keyCode == KEY_CODE.ESCAPE) {
      this.tabService.removeActiveTab();
    }

    if (
      (event.keyCode >= KEY_CODE.ONE && event.keyCode <= KEY_CODE.NINE) ||
      (event.keyCode >= KEY_CODE.PAD_ONE && event.keyCode <= KEY_CODE.PAD_NINE)
    ) {
      this.tabService.activeTabById(+event.key);
    }
  }
  ngOnInit(): void {}
}
