import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'app-csv-import-uploadbox',
  templateUrl: './csv-import-uploadbox.component.html',
})
export class CSVImportUploadboxComponent {
  @Output() public removeFile: EventEmitter<boolean> = new EventEmitter();
  @Input() public loading: boolean = false;
  @Input() public file: any;
  @Input() public fileSize: any;
  @Input() public rowsCount: string;

  public removeSelectedFile(): void {
    this.removeFile.emit(true);
  }
}
