import { Component, OnInit } from '@angular/core';
import { ProgressBarService } from '../../../shared/services/progress-bar.service';
import { ProgressBar } from '../../../shared/definitions';

@Component({
  selector: 'app-progress-bar-list',
  templateUrl: './progress-bar-list.component.html',
  styleUrls: ['./progress-bar-list.component.scss'],
})
export class ProgressBarListComponent implements OnInit {
  public progressBars: Array<ProgressBar> = [];
  constructor(private progressBarService: ProgressBarService) {
    progressBarService.progressBar.subscribe((result) => {
      this.progressBars = result;
    });
  }

  ngOnInit(): void {}
}
