import { ApplicationRef, Injectable } from '@angular/core';
import { IpcRenderer } from 'electron';
import { IotEventTypes } from '../../../../ipc-helpers/definitions';
import { RealtimeService } from './realtime.service';
import { StoreService } from './store.service';

@Injectable({ providedIn: 'root' })
export class IPCService {
  private ipc: IpcRenderer;

  constructor(
    private realtimeService: RealtimeService,
    private storeService: StoreService,
    private ref: ApplicationRef
  ) {
    if ((<any>window).require) {
      try {
        this.ipc = (<any>window).require('electron').ipcRenderer;
      } catch (e) {
        throw e;
      }
    } else {
      console.warn('App not running inside Electron!');
    }

    if (!this.ipc) {
      return;
    }

    this.ipc.on(IotEventTypes.IncomingMessage, (type, data) => {
      this.realtimeService.handleIncomingData(data);
    });

    this.ipc.on(IotEventTypes.HostNetworkInformationChange, (type, data) => {
      this.storeService.networkInformation.next(data);
      this.ref.tick();
    });

    // This is now handled by socket io. Review if it will be needed for release v2
    // this.ipc.on(IotEventTypes.BridgeConnect, (type, data) => {
    //   this.storeService.bridgeConnected.next(data);
    //   this.ref.tick();
    // });

    this.ipc.send(IotEventTypes.BridgeConnect, null);
  }

  public requestInstall(): void {
    this.ipc.send(IotEventTypes.RequestInstallServer);
  }

  public requestUninstall(): void {
    this.ipc.send(IotEventTypes.RequestUninstallServer);
  }
}
