import { Injectable } from '@angular/core';
import { SelectVisualiztionComponent } from 'app/iot/components/select-visualization-list/select-visualization-list.component';
import { filter, switchMap } from 'rxjs/operators';
import { DataService } from './data.service';
import { ModalService } from './modal.service';

@Injectable({
  providedIn: 'root',
})
export class VisualizationService {
  constructor(
    private modalService: ModalService,
    private dataService: DataService
  ) {}

  /**
   * User can select what kind of visualiztion they want to add into the report
   */
  promptAddVisualization(): void {
    this.modalService
      .open({
        title: 'Add visualization',
        content: SelectVisualiztionComponent,
      })
      .pipe(
        filter((result) => result.type === 'CONFIRMED'),
        switchMap((result) =>
          this.dataService.createVisualElement(result.data, 1)
        )
      )

      .subscribe();
  }
}
