import { Component, OnDestroy, OnInit } from '@angular/core';
import {
  DeviceRichInformation,
  Response,
} from '../../../../../ipc-helpers/definitions';
import { DataService } from '../../../shared/services/data.service';
import { catchError, filter, switchMap, tap } from 'rxjs/operators';
import { ColumnMode, SelectionType } from '@swimlane/ngx-datatable';
import { NavigatorService } from 'app/shared/services/navigator.service';
import { NotificationService } from 'app/shared/services/notification.service';
import { ModalDialogType } from 'app/shared/definitions';
import { ModalService } from 'app/shared/services/modal.service';
import { translations } from 'app/locale/translations';
import { HttpErrorResponse } from '@angular/common/http';
import { throwError } from 'rxjs';
import { IResponse } from 'response-type';

@Component({
  selector: 'app-devices',
  templateUrl: './devices.component.html',
  styleUrls: [],
})
export class DevicesComponent implements OnInit, OnDestroy {
  public devices: DeviceRichInformation[] = [];
  private subscriptions = [];
  public loading = true;
  public res: Response<DeviceRichInformation>;

  public page = {
    count: 10,
    offset: 0,
    limit: 10,
  };

  ColumnMode = ColumnMode;
  SelectionType = SelectionType;

  constructor(
    private dataService: DataService,
    private navigatorService: NavigatorService,
    private modalService: ModalService,
    private notificationService: NotificationService
  ) {}

  public get hasNoDevice() {
    return !this.loading && this.devices && this.devices.length === 0;
  }

  onActivate(event) {
    event.type === 'click' && event.cellElement && event.cellElement.blur();
  }

  select(event) {
    if (event.type === 'click') {
      setTimeout(() => {
        this.navigatorService.openViewDeviceTab(event.row);
      }, 0);
    }
  }

  setPage(event) {
    const offset = event.offset * event.pageSize;
    this.queryDevice(offset, event.limit);
  }

  ngOnInit(): void {
    this.queryDevice();
  }

  private queryDevice(offset = 0, limit = 10): void {
    this.dataService
      .getDevices(offset, limit)
      .pipe(
        tap((res) => {
          this.res = res;
          this.page = {
            count: res.data.totalItems,
            offset: res.data.pageIndex,
            limit: res.data.itemsPerPage,
          };

          this.devices = this.res.data.items;
          this.loading = false;
        })
      )
      .subscribe();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((sub) => sub && sub.unsubscribe());
  }

  readData(device: DeviceRichInformation) {
    return Object.keys(device.data).map((key: string) => {
      return `${key}: ${device.data[key]}`;
    });
  }

  public deleteItem(event: any, row: any) {
    event.preventDefault();
    event.stopPropagation();

    const commonHttpErrorHandler = catchError((error) => {
      if (error instanceof HttpErrorResponse) {
        this.notificationService.showFailedMessage(
          error.status.toString(),
          error.message
        );
      }
      return throwError(error);
    });

    this.modalService
      .open({
        type: ModalDialogType.CONFIRMATION,
        title: translations.devices.deleteTitle,
        description: translations.devices.deleteDescription,
      })
      .pipe(
        filter((res) => res.type === 'CONFIRMED'),
        switchMap(() => this.dataService.deleteDevice(row.id)),
        tap((res: IResponse<any>) => {
          if (res.data.deleted) {
            this.devices = this.devices.filter(
              (device) => device.id !== row.id
            );
            this.page = {
              ...this.page,
              count: this.page.count - 1,
            };
          }
        }),
        commonHttpErrorHandler
      )
      .subscribe();
  }
}
