import { Component, OnDestroy, OnInit } from '@angular/core';
import { translations } from 'app/locale/translations';
import {
  ActivePort,
  ActivePortSubscription,
  SerialPort,
  ModalDialogType,
} from 'app/shared/definitions';
import { ModalService } from 'app/shared/services/modal.service';
import { NavigatorService } from 'app/shared/services/navigator.service';
import { interval, Subscription } from 'rxjs';
import { filter, switchMap, tap } from 'rxjs/operators';
import { DataService } from '../../../shared/services/data.service';

@Component({
  selector: 'app-active-streams',
  templateUrl: './active-streams.component.html',
  styleUrls: ['./active-streams.component.scss'],
})
export class ActiveStreamsComponent implements OnInit, OnDestroy {
  public activeStreams: ActivePortSubscription[] = [];
  public translations = translations;
  private subscriptions: Subscription[] = [];
  public serialPorts: SerialPort[] = [];
  public activePorts: ActivePort[] = [];
  public serverInformation: any = null;

  constructor(
    private dataService: DataService,
    private modalService: ModalService,
    public navigatorService: NavigatorService
  ) {}

  ngOnInit(): void {
    this.subscriptions = [
      this.dataService
        .activeStreams$()
        .pipe(
          tap((items: ActivePortSubscription[]) => {
            this.activeStreams = items;
          })
        )
        .subscribe(),
      this.dataService
        .serialPorts$()
        .pipe(
          tap((items: SerialPort[]) => {
            this.serialPorts = items;
          })
        )
        .subscribe(),
      interval(5000)
        .pipe(switchMap(() => this.dataService.getSerialPorts()))
        .subscribe(),

      this.dataService
        .getActivePorts()
        .pipe(
          tap((items) => {
            this.activePorts = items;
          })
        )
        .subscribe(),
      this.dataService
        .getServerInformation()
        .pipe(
          tap((information) => {
            this.serverInformation = information;
          })
        )
        .subscribe(),
    ];
  }

  ngOnDestroy(): void {
    this.subscriptions
      .filter(Boolean)
      .forEach((subscription) => subscription.unsubscribe());
  }

  killProcess(id: string) {
    this.dataService.killStream(id).subscribe();
  }

  public deleteActivePort(id: number) {
    this.modalService
      .open({
        type: ModalDialogType.CONFIRMATION,
        title: translations.activeStreams.deletePortTitle,
        description: translations.activeStreams.deletePortDescription,
      })
      .pipe(
        filter((data) => data.type === 'CONFIRMED'),
        switchMap(() => this.dataService.deleteActivePort(id)),
        tap(() => {
          this.activePorts = this.activePorts.filter((item) => item.id !== id);
        })
      )
      .subscribe();
  }
}
