import { Component, Input, OnDestroy, ViewChild } from '@angular/core';
import * as Papa from 'papaparse';
import { DataService } from 'app/shared/services/data.service';
import { CSVImportOptions } from 'app/shared/definitions';
import { NotificationService } from '../../../shared/services/notification.service';
import { ImportJobsStatus } from '../../../shared/definitions';
import { detectDataType, detectCSVHeader } from './import-tools';
import { Subscription } from 'rxjs';
import { fileSize, numberWithCommas } from '../../../shared/utils';
import { ImportService } from '../../../shared/services/import.service';
import { ActivatedRoute } from '@angular/router';
import { translations } from 'app/locale/translations';
import { CSVImportPreviewComponent } from '../csv-import-preview/csv-import-preview.component';

@Component({
  selector: 'app-csv-import',
  templateUrl: './csv-import.component.html',
  styleUrls: [],
})
export class CSVImportComponent implements OnDestroy {
  @ViewChild(CSVImportPreviewComponent, { static: false })
  public viewPort: CSVImportPreviewComponent;

  @Input() public params: { file?: any } = {};

  public file: any;
  public fileSize: string;
  public rowsCount: string;
  public loading: boolean = false;
  public fieldNames = [];
  public imei: string = '';
  public name: string = '';
  public include: boolean[] = [];
  public dataTypes = [];
  public skipRows = 1;
  public contentPreview: any = null;
  public contentTypeGuess: any[] = [];
  public activeTabsIndex: number = 0;
  public isImporting: boolean;
  public jobId: number = 0;
  public linesProcessed: number = 0;

  private statusSubscription: Subscription;

  constructor(
    private dataService: DataService,
    private notification: NotificationService,
    private importService: ImportService,
    private route: ActivatedRoute
  ) {
    this.importService.jobs.subscribe((result) => {
      const pageJob = result.find((x) => x.jobId == this.jobId);
      if (!pageJob) {
        return;
      }
      if (pageJob.status === ImportJobsStatus.ACTIVE) {
        this.isImporting = true;
        this.linesProcessed = pageJob.linesProccessed;
      } else if (pageJob.status === ImportJobsStatus.CANCELED) {
        this.isImporting = false;
        this.linesProcessed = pageJob.linesProccessed;
      } else if (pageJob.status === ImportJobsStatus.FINISHED) {
        this.isImporting = false;
        this.linesProcessed = pageJob.totalLines;
      }

      this.viewPort.scroll(this.linesProcessed || 0);
    });

    this.route.params.subscribe((e) => {
      this.removeSelectedFile();
      if (this.params && this.params.file) {
        this.fileChanged(this.params.file);
      }
    });
  }

  fileChanged(file) {
    this.file = file;
    this.loading = true;
    let fileReader = new FileReader();
    fileReader.onload = () => {
      const config: any = {
        header: false,
      };

      const text = fileReader.result.toString();

      let lines: string[][] = Papa.parse(text, config).data;

      if (lines[lines.length - 1].length === 1) {
        lines.pop();
      }

      this.contentPreview = lines;
      const { items, skipRows } = detectCSVHeader(lines);
      this.skipRows = skipRows;
      this.fieldNames = items;
      this.include = this.fieldNames.map(() => true);
      this.contentTypeGuess = detectDataType(this.contentPreview);
      this.dataTypes = this.contentTypeGuess;
      this.loading = false;
      this.fileSize = fileSize(this.file.size);
      this.rowsCount = numberWithCommas(this.contentPreview?.length - 1);
    };
    fileReader.readAsText(file);
  }

  public get options(): CSVImportOptions {
    return {
      include: this.include,
      dataTypes: this.dataTypes,
      fieldNames: this.fieldNames,
      skipRows: this.skipRows,
      imei: this.imei,
      name: this.name,
    };
  }

  setDataType(i, event) {
    if (event.target.value == 'creationdate') {
      let count = 0;
      this.dataTypes.forEach((x, i) => {
        if (x == 'creationdate' && this.include[i]) {
          count++;
        }
      });
      if (count > 0) {
        this.notification.showFailedMessage(
          translations.csvImport.doubleCreationDateError,
          translations.csvImport.doubleCreationDateErrorDescription
        );

        this.dataTypes = [...this.dataTypes];
      } else {
        this.dataTypes[i] = event.target.value;
      }
    } else {
      this.dataTypes[i] = event.target.value;
    }
  }

  getDataType(i) {
    return this.dataTypes[i];
  }

  public toggleAllIncludes(e: any): void {
    this.include = this.include.map(() => e.target.checked);
  }

  /**
   * Check CreationDate before executing data
   */
  public checkDataTypes(): boolean {
    let count = 0,
      i = 0;
    for (let type of this.dataTypes) {
      if (type === 'creationdate' && this.include[i]) {
        count++;
      }
      i++;
    }

    if (count === 0) {
      this.notification.showFailedMessage(
        translations.csvImport.missingCreationDateTitle,
        translations.csvImport.missingCreationDateDescription
      );
      this.activeTabsIndex = 1;
      return false;
    }
    if (count > 1) {
      this.notification.showFailedMessage(
        translations.csvImport.moreThanOneTitle,
        translations.csvImport.moreThanOneDescription
      );
      this.activeTabsIndex = 1;
      return false;
    }

    return true;
  }

  /**
   * Check include variable before executing data
   */
  public checkIncludes(): boolean {
    let count = 0;
    for (let item of this.include) {
      if (item) {
        count++;
      }
    }

    if (count < 2) {
      this.notification.showFailedMessage(
        translations.csvImport.atLeastTwoColumnTitle,
        translations.csvImport.atLeastTwoColumnDescription
      );

      this.activeTabsIndex = 1;
      return false;
    }
    return true;
  }

  /**
   * Check active fields name before executing data
   */
  public checkFieldsName(): boolean {
    let i = 0;
    for (let type of this.fieldNames) {
      if ((!type || type.length === 0) && this.include[i]) {
        this.notification.showFailedMessage(
          translations.csvImport.fieldNameMissingTitle,
          translations.csvImport.fieldNameMissingDescription
        );

        this.activeTabsIndex = 1;
        return false;
      }
      i++;
    }
    return true;
  }

  public beginImport(): void {
    if (
      !this.checkFieldsName() ||
      !this.checkIncludes() ||
      !this.checkDataTypes()
    ) {
      return;
    }

    this.dataService.importCSV(this.file, this.options).subscribe((result) => {
      this.jobId = result.data.jobId;
      if (this.jobId !== 0) {
        this.notification.showSuccessMessage(
          translations.csvImport.startImportingTitle,
          translations.csvImport.startImportingDescription
        );
      } else {
        this.notification.showFailedMessage(
          translations.csvImport.unknownErrorTitle,
          translations.csvImport.unknownErrorDescription
        );
      }
    });
  }

  removeSelectedFile() {
    this.file = undefined;
    this.loading = false;
    this.fieldNames = [];
    this.imei = '';
    this.name = '';
    this.include = [];
    this.dataTypes = [];
    this.skipRows = 1;
    this.contentPreview = null;
    this.contentTypeGuess = [];
  }

  stopJob() {
    this.dataService.importStop(this.jobId).subscribe();
  }

  ngOnDestroy(): void {
    this.statusSubscription && this.statusSubscription.unsubscribe();
  }
}
